import { useContext, useEffect } from 'preact/hooks';
import { ThemeContext } from 'src/core/containers';
import { THEME } from 'src/core/models';

export function useRubicWidget() {
  const { theme } = useContext(ThemeContext);

  useEffect(() => {
    const configuration = {
      from: 'ETH',
      to: 'RBC',
      fromChain: 'ETH',
      toChain: 'ETH',
      amount: 1,
      iframe: 'flex',
      hideSelectionFrom: false,
      hideSelectionTo: true,
      theme: theme === THEME.DARK ? 'dark' : 'light',
      background: '#28372e',
      injectTokens: {
        eth: ['0xd123575d94a7ad9bff3ad037ae9d4d52f41a7518'],
        bsc: ['0x8aed24bf6e0247be51c57d68ad32a176bf86f4d9']
      }
    };

    // prevent accidental changes to the object, for example, when re-creating a widget for another theme
    Object.freeze(configuration);

    // create widget
    rubicWidget.init(configuration);
  }, [theme]);
}
