import { useWindowWidth } from '@react-hook/window-size';
import { FunctionalComponent, h, Fragment } from 'preact';
import cn from 'classnames';
import { useRubicWidget } from 'src/core/hooks/useRubicWidget';
import { Text } from 'preact-i18n';
import { useState } from 'preact/hooks';
import { ExternalLinks } from 'src/shared/Constants';
import style from './style.module.scss';
import GetWidgetModal from '../../../../shared/components/Modal/components/GetWidgetModal';

const Trade: FunctionalComponent = () => {
  const [modal, setModal] = useState(false);
  useRubicWidget();
  const windowWidth = useWindowWidth();
  const isMobile = windowWidth < 1180;

  const linkButton = (
    <Fragment>
      <button onClick={() => setModal(true)} class={cn('button', style.trade__button)}>
        <Text id="trade.getWidget">Get Widget</Text>
        <img src="assets/icons/arrow-right.svg" alt="Get widget" />
      </button>
      <GetWidgetModal
        text={
          <Fragment>
            <Text id="modal.theWidgetOnlySupports">
              The widget only supports tokens that are listed at Rubic.exchange. In order to be
              listed, please fill the form.
            </Text>{' '}
            <a
              href={ExternalLinks.docWidget}
              target="_blank"
              rel="noreferrer"
            >
              <span class="highlight">
                <Text id="modal.checkDocs">Check docs</Text>
              </span>
            </a>{' '}
            <Text id="modal.forSelfIntegration">for selfintegration.</Text>{' '}
          </Fragment>
        }
        open={modal}
        onModalClose={() => {
          setModal(false);
        }}
      />
    </Fragment>
  );

  return (
    <section class={style.trade}>
      <h2 class={style['trade__title']}>
        <Text id="trade.trade">Trade</Text>{' '}
        <br class={style.trade__br} />
        <span class="highlight">
          <Text id="trade.yourToken">your token</Text>
        </span>
        <br />
        <Text id="trade.directlyOn">directly on</Text>
        <br />
        <span class="highlight">
          <Text id="trade.yourPlatform">your platform!</Text>
        </span>
      </h2>

      <p class={style['trade__text']}>
        <Text id="trade.theRubicRelay">The Rubic Relay</Text>{' '}
        <span class="highlight">
          <Text id="trade.widgetEnables">Widget enables</Text>
        </span>{' '}
        <Text id="trade.users">
          users
        </Text>{' '}
        <span class="highlight">
          <Text id="trade.toBuy">
            to buy and sell tokens on any website.
          </Text>{' '}
        </span>
        <Text id="trade.thisMeans">
          This means that your users can buy token, without having to leave your website! It’s
        </Text>{' '}
        <span class="highlight">
          <Text id="trade.free">free</Text>{' '}
        </span>{' '}
        <Text id="trade.and">and</Text>{' '}
        <span class="highlight">
          <Text id="trade.easy">easy to setup.</Text>
        </span>
      </p>

      {!isMobile && linkButton}

      <div style={{ position: 'relative' }}>
        <div className={style['rubic-widget-root']} id="rubic-widget-root" />
      </div>

      {isMobile && linkButton}

      <div class={style.gradient} />
    </section>
  );
};

export default Trade;
