import { Fragment, FunctionalComponent, h, VNode } from 'preact';
import Modal from 'src/shared/components/Modal';
import { Text } from 'preact-i18n';
import { useFormik } from 'formik';
import { useState } from 'preact/hooks';
import style from './style.module.scss';
import SuccessModal from '../SuccessModal';

type ModalProps = {
  open: boolean;
  onModalClose: () => void;
  text: VNode<unknown> | string;
};

const GetWidgetModal: FunctionalComponent<ModalProps> = ({ onModalClose, text, open }) => {
  const [modal, setModal] = useState(false);

  const formik = useFormik({
    initialValues: {
      name: '',
      email: '',
      projectName: '',
      projectLinks: '',
      contractAddress: '',
      description: ''
    },
    onSubmit: values => {
      fetch('https://api.rubic.exchange/api/email/partnership', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(values)
      })
        .then(() => {
          onModalClose();
          setModal(true);
        })
        .catch(e => {
          console.error(e);
        });
    }
  });

  return (
    <Fragment>
      <Modal open={open} onModalClose={onModalClose}>
        <div class={style['widget-modal']}>
          <p class={style['widget-modal__text']}>{text}</p>
          <form
            class={style['widget-modal__form']}
            id="getWidgetForm"
            name="getWidgetForm"
            onSubmit={formik.handleSubmit as any}
          >
            <div class={style['widget-modal__container']}>
              <input
                class={style['widget-modal__input']}
                type="text"
                name="projectName"
                placeholder="Project name"
                required
                onChange={formik.handleChange}
                value={formik.values.projectName}
              />
              <input
                class={style['widget-modal__input']}
                type="text"
                name="projectLinks"
                placeholder="Project links / Social media"
                required
                onChange={formik.handleChange}
                value={formik.values.projectLinks}
              />
              <input
                class={style['widget-modal__input']}
                type="text"
                name="contractAddress"
                placeholder="Contract address"
                onChange={formik.handleChange}
                value={formik.values.contractAddress}
              />
              <input
                class={style['widget-modal__input']}
                type="text"
                name="email"
                placeholder="Contact email"
                required
                onChange={formik.handleChange}
                value={formik.values.email}
              />
              <textarea
                class={style['widget-modal__textarea']}
                name="description"
                placeholder="Project description"
                onChange={formik.handleChange}
                value={formik.values.description}
              />
            </div>

            <button class={style['widget-modal__submit']} type="submit">
              <Text id="modal.submit">Submit</Text>
            </button>
          </form>
        </div>
      </Modal>
      <SuccessModal
        title={
          <Text id="modal.theApplicationHasBeenSubmitted">
            Thank you! The application has been submitted
          </Text>
        }
        open={modal}
        onModalClose={() => setModal(false)}
      />
    </Fragment>
  );
};

export default GetWidgetModal;
