import { FunctionalComponent, h, VNode } from 'preact';
import { useState } from 'preact/hooks';
import { warning } from 'src/routes/contact/components/Feedback/Constants';
import style from 'src/routes/main/components/MainScreen/components/Advantage/style.module.scss';
import { THEME } from '../../../../../../core/models';

interface AdvantageProps {
  img: string;
  title: VNode<unknown>;
  text: VNode<unknown> | string;
}

const Advantage: FunctionalComponent<AdvantageProps> = props => {
  const [click, setClick] = useState(0);

  if (click >= 7) {
    alert(warning);
  }

  return (
    <div className={style.advantage} onClick={() => setClick(click + 1)}>
      <img className={style.advantage__img} src={props.img} alt="Advantage image" />

      <h3 className={style.advantage__title}>{props.title}</h3>

      <p className={style.advantage__text}>{props.text}</p>
    </div>
  );
};

export default Advantage;
